import React, {Component} from "react";
import Representative from "eshop/modules/checkout/components/representative/Representative";

export default class Representatives extends Component {

    constructor(props) {
        super(props);
        this.removeFactory = this.removeFactory.bind(this);
        this.ordinals = ["Pierwszy", "Drugi", "Trzeci", "Czwarty", "Piąty", "Szósty", "Siódmy", "Ósmy", "Dziewiąty", "Dziesiąty"];
    }

    removeFactory() {
        if(this.props.minCount && this.props.data.length > this.props.minCount) {
            return this.props.remove;
        }
    }

    getLabel(index) {
        if (this.props.data.length > 1) {
            let label = "";
            if(this.props.representationMode == "WR") {
                label = "reprezentant";
            }
            if(this.props.representationMode == "WP" ) {
                label = "pełnomocnik";
            }
            return this.ordinals[index] + " " + label;
        } else {
            return null;
        }
    }

    render() {
        return (
            <div>
                {
                    this.props.data.map((data, index) =>
                        <Representative
                            {...this.props}
                            {...data}
                            key={index}
                            index={index}
                            onChange={this.props.onChange}
                            remove={this.removeFactory()}
                            label={this.getLabel(index)}
                        />
                    )
                }
            </div>
        );
    }
}